import { useEffect, useState, useRef } from 'react'
import { letters, status } from './constants'
import { Keyboard } from './Keyboard'
import answers from './data/answers'
import words from './data/words'
import Modal from 'react-modal'
import Success from './data/Success.png'
import Fail from './data/Cross.png'
import { useLocalStorage } from './hooks/useLocalStorage'
import { ReactComponent as Github } from './data/Github.svg'
import { ReactComponent as Close } from './data/Close.svg'
import { ReactComponent as Info } from './data/Info.svg'

Modal.setAppElement('#root')

const state = {
  playing: 'playing',
  won: 'won',
  lost: 'lost',
}

function reverse(s) {
  return [...s].reverse().join("");
}

function invert(json) {
  var ret = {};
  for (var key in json) {
    ret[json[key]] = key;
  }
  return ret;
}

const finalFormLetters = {
                             'ך': 'כ',
                             'ם': 'מ',
                             'ן': 'נ',
                             'ף': 'פ',
                             'ץ': 'צ'
                            }
const invertedFinalFormLetters = invert(finalFormLetters)                            

const getRandomAnswer = () => {
  const randomIndex = Math.floor(Math.random() * answers.length)
  return reverse(answers[randomIndex].toUpperCase())
}

function App() {
  const initialStates = {
    answer: () => getRandomAnswer(),
    gameState: state.playing,
    board: [
      ['', '', '', '', ''],
      ['', '', '', '', ''],
      ['', '', '', '', ''],
      ['', '', '', '', ''],
      ['', '', '', '', ''],
      ['', '', '', '', ''],
    ],
    cellStatuses: () => Array(6).fill(Array(5).fill(status.unguessed)),
    currentRow: 0,
    currentCol: 0,
    letterStatuses: () => {
      const letterStatuses = {}
      letters.forEach((letter) => {
        letterStatuses[letter] = status.unguessed
      })
      return letterStatuses
    },
  }
  const [answer, setAnswer] = useState(initialStates.answer)
  const [gameState, setGameState] = useState(initialStates.gameState)
  const [board, setBoard] = useState(initialStates.board)
  const [cellStatuses, setCellStatuses] = useState(initialStates.cellStatuses)
  const [currentRow, setCurrentRow] = useState(initialStates.currentRow)
  const [currentCol, setCurrentCol] = useState(initialStates.currentCol)
  const [letterStatuses, setLetterStatuses] = useState(initialStates.letterStatuses)
  const [submittedInvalidWord, setSubmittedInvalidWord] = useState(false)
  const [currentStreak, setCurrentStreak] = useLocalStorage('current-streak', 0)
  const [longestStreak, setLongestStreak] = useLocalStorage('longest-streak', 0)
  const streakUpdated = useRef(false)
  const [modalIsOpen, setIsOpen] = useState(false)
  const [firstTime, setFirstTime] = useLocalStorage('first-time', true)
  const [infoModalIsOpen, setInfoModalIsOpen] = useState(firstTime)

  const openModal = () => setIsOpen(true)
  const closeModal = () => setIsOpen(false)
  const handleInfoClose = () => {
    setFirstTime(false)
    setInfoModalIsOpen(false)
  }

  useEffect(() => {
    if (gameState !== state.playing) {
      setTimeout(() => {
        openModal()
      }, 500)
    }
  }, [gameState])

  useEffect(() => {
    if (!streakUpdated.current) {
      if (gameState === state.won) {
        if (currentStreak >= longestStreak) {
          setLongestStreak((prev) => prev + 1)
        }
        setCurrentStreak((prev) => prev + 1)
        streakUpdated.current = true
      } else if (gameState === state.lost) {
        setCurrentStreak(0)
        streakUpdated.current = true
      }
    }
  }, [gameState, currentStreak, longestStreak, setLongestStreak, setCurrentStreak])

  const getCellStyles = (rowNumber, colNumber, letter) => {
    if (rowNumber === currentRow) {
      if (letter) {
        return `nm-inset-background text-primary ${
          submittedInvalidWord ? 'border border-red-800' : ''
        }`
      }
      return 'nm-flat-background text-primary'
    }

    switch (cellStatuses[rowNumber][colNumber]) {
      case status.green:
        return 'nm-inset-n-green text-gray-50'
      case status.yellow:
        return 'nm-inset-yellow-500 text-gray-50'
      case status.gray:
        return 'nm-inset-n-gray text-gray-50'
      default:
        return 'nm-flat-background text-primary'
    }
  }

  const addLetter = (letter) => {
    setSubmittedInvalidWord(false)
    setBoard((prev) => {
      if (currentCol > 4) {
        return prev
      }
      const newBoard = [...prev]
      newBoard[currentRow][4 - currentCol] = letter
      return newBoard
    })
    if (currentCol < 5) {
      setCurrentCol((prev) => prev + 1)
    }
  }

  const isValidWord = (word) => {
    if (word.length < 5) return false
    return words[reverse(word.toLowerCase())]
  }

  const onEnterPress = () => {
    const word = board[currentRow].join('')
    if (!isValidWord(word)) {
      setSubmittedInvalidWord(true)
      return
    }

    if (currentRow === 6) return

    updateCellStatuses(word, currentRow)
    updateLetterStatuses(word)
    setCurrentRow((prev) => prev + 1)
    setCurrentCol(0)
  }

  const onDeletePress = () => {
    setSubmittedInvalidWord(false)
    if (currentCol === 0) return

    setBoard((prev) => {
      const newBoard = [...prev]
      newBoard[currentRow][5 - currentCol] = ''
      return newBoard
    })

    setCurrentCol((prev) => prev - 1)
  }

  const updateCellStatuses = (word, rowNumber) => {
    setCellStatuses((prev) => {
      const newCellStatuses = [...prev]
      newCellStatuses[rowNumber] = [...prev[rowNumber]]
      const wordLength = word.length
      let answerLetters = answer.split("")
      const lastLetter = answerLetters[0]
      if (lastLetter in finalFormLetters){
        answerLetters[0] = finalFormLetters[lastLetter]
      }

      // set all to gray
      for (let i = 0; i < wordLength; i++) {
        newCellStatuses[rowNumber][i] = status.gray
      }

      // check greens
      for (let i = wordLength - 1; i >= 0; i--) {
        if (word[i] === answer[i]) {
          newCellStatuses[rowNumber][i] = status.green
          answerLetters.splice(i, 1)
        }
      }

      // check yellows
      for (let i = wordLength - 1; i >= 0; i--) {
        if (answerLetters.includes(word[i]) && newCellStatuses[rowNumber][i] !== status.green) {
          newCellStatuses[rowNumber][i] = status.yellow
          answerLetters.splice(answerLetters.indexOf(word[i]), 1)
        }
      }

      return newCellStatuses
    })
  }

  const isRowAllGreen = (row) => {
    return row.every((cell) => cell === status.green)
  }

  // every time cellStatuses updates, check if the game is won or lost
  useEffect(() => {
    const cellStatusesCopy = [...cellStatuses]
    const reversedStatuses = cellStatusesCopy.reverse()
    const lastFilledRow = reversedStatuses.find((r) => {
      return r[0] !== status.unguessed
    })

    if (lastFilledRow && isRowAllGreen(lastFilledRow)) {
      setGameState(state.won)
    } else if (currentRow === 6) {
      setAnswer(reverse(answer))
      setGameState(state.lost)
    }
  }, [cellStatuses, currentRow])

  const updateLetterStatuses = (word) => {
    setLetterStatuses((prev) => {
      const newLetterStatuses = { ...prev }
      const wordLength = word.length
      for (let i = 0; i < wordLength; i++) {
        let letter = word[i]
        let finalFormLetter = letter;
        if (letter in invertedFinalFormLetters){
          finalFormLetter = invertedFinalFormLetters[letter]
        }
        if (newLetterStatuses[letter] === status.green) continue

        if (letter === answer[i]) {
          newLetterStatuses[letter] = status.green
        } else if (answer.includes(letter) || (answer.includes(finalFormLetter))) {
          newLetterStatuses[letter] = status.yellow
        } else {
          newLetterStatuses[letter] = status.gray
        }
      }
      return newLetterStatuses
    })
  }

  const PlayAgainButton = () => {
    return (
      <button
        type="button"
        className="rounded-lg px-6 py-2 mt-8 text-lg nm-flat-background hover:nm-inset-background"
        onClick={() => {
          setAnswer(initialStates.answer)
          setGameState(initialStates.gameState)
          setBoard(initialStates.board)
          setCellStatuses(initialStates.cellStatuses)
          setCurrentRow(initialStates.currentRow)
          setCurrentCol(initialStates.currentCol)
          setLetterStatuses(initialStates.letterStatuses)
          closeModal()
          streakUpdated.current = false
        }}
      >
        שחק שוב
      </button>
    )
  }

  return (
    <div className="flex flex-col justify-between h-fill bg-background">
      <header className="flex items-center py-2 px-3 text-primary">
        <h1 className="flex-1 text-center text-xl xxs:text-2xl -mr-6 sm:text-4xl tracking-wide font-bold">
          בול במילה
        </h1>
        <button type="button" onClick={() => setInfoModalIsOpen(true)}>
          <Info />
        </button>
      </header>
      <div className="flex items-center flex-col py-3">
        <div className="grid grid-cols-5 grid-flow-row gap-4">
          {board.map((row, rowNumber) =>
            row.map((letter, colNumber) => (
              <span
                key={colNumber}
                className={`${getCellStyles(
                  rowNumber,
                  colNumber,
                  letter
                )} inline-flex items-center justify-center text-lg font-medium w-[14vw] h-[14vw] xs:w-14 xs:h-14 sm:w-20 sm:h-20 rounded-full`}
              >
                {letter}
              </span>
            ))
          )}
        </div>
      </div>
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Game End Modal"
      >
        <div className="h-full flex flex-col items-center justify-center max-w-[300px] mx-auto">
          {gameState === state.won && (
            <>
              <img src={Success} alt="success" height="auto" width="auto" />
              <h1 className="text-primary text-3xl rtl">כל הכבוד!</h1>
              <p className="mt-6 rtl">
                רצף נוכחי: <strong>{currentStreak}</strong> {currentStreak > 4 && '🔥'}
              </p>
              <p className="rtl">
                הרצף הכי ארוך: <strong>{longestStreak}</strong>
              </p>
            </>
          )}
          {gameState === state.lost && (
            <>
              <img src={Fail} alt="success" height="auto" width="80%" />
              <div className="text-primary text-4xl text-center">
                <p className="rtl">לא נורא</p>
                <p className="mt-3 text-2xl rtl">
                  המילה הייתה <strong>{answer}</strong>.
                </p>
                <p className="mt-6 text-base rtl">
                  רצף נוכחי: <strong>{currentStreak}</strong> {currentStreak > 4 && '🔥'}
                </p>
                <p className="text-base rtl">
                  הרצף הכי ארוך: <strong>{longestStreak}</strong>
                </p>
              </div>
            </>
          )}
          <PlayAgainButton />
        </div>
      </Modal>
      <Keyboard
        letterStatuses={letterStatuses}
        addLetter={addLetter}
        onEnterPress={onEnterPress}
        onDeletePress={onDeletePress}
        gameDisabled={gameState !== state.playing}
      />
      <Modal
        isOpen={infoModalIsOpen}
        onRequestClose={handleInfoClose}
        style={customStyles}
        contentLabel="Game Info Modal"
      >
        <button
          className="absolute top-4 right-4 rounded-full nm-flat-background text-primary p-1 w-6 h-6 sm:p-2 sm:h-8 sm:w-8"
          onClick={handleInfoClose}
        >
          <Close />
        </button>
        <div className="h-full flex flex-col items-center justify-center max-w-[390px] mx-auto pt-9 text-primary">
          <div className="flex-1 w-full border sm:text-base text-sm">
            <h1 className="text-center sm:text-3xl text-2xl rtl">איך לשחק?</h1>
            <ul className="list-disc pl-5 block sm:text-base text-sm">
              <li className="mt-6 mb-2 rtl">יש לך 6 ניחושים לנחש את המילה הנכונה</li>
              <li className="mb-2 rtl">אפשר לנחש כל מילה שמופיעה ברשימת המילים של המשחק</li>
              <li className="mb-2 rtl">
                אחרי כל ניחוש, כל אות תיצבע בירוק, צהוב או אפור.
              </li>
            </ul>
            <div className="mb-3 mt-8 flex items-center rtl">
              <span className="nm-inset-n-green text-gray-50 inline-flex items-center justify-center text-3x w-10 h-10 rounded-full">
                א
              </span>
              <span className="mx-2">=</span>
              <span>אות נכונה, במיקום הנכון.</span>
            </div>
            <div className="mb-3 rtl">
              <span className="nm-inset-yellow-500 text-gray-50 inline-flex items-center justify-center text-3x w-10 h-10 rounded-full">
                א
              </span>
              <span className="mx-2">=</span>
              <span>אות נכונה, במיקום לא נכון.</span>
            </div>
            <div className="mb-3 rtl">
              <span className="nm-inset-n-gray text-gray-50 inline-flex items-center justify-center text-3x w-10 h-10 rounded-full">
                א
              </span>
              <span className="mx-2">=</span>
              <span>האות לא מופיעה במילה.</span>
            </div>


          </div>
          <div className="flex justify-center sm:text-base text-sm">
            <a
              className="ml-[6px] rounded-full h-5 w-5 sm:h-6 sm:w-9"
              href="https://github.com/aviv57/word-master"
              target="_blank"
              rel="noreferrer"
            >
              <Github />
            </a>
            <span>הקוד של המשחק זמין ב</span>
          </div>
        </div>
      </Modal>
    </div>
  )
}

const customStyles = {
  overlay: {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'hsl(231, 16%, 92%)',
  },
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    transform: 'translate(-50%, -50%)',
    height: 'calc(100% - 2rem)',
    width: 'calc(100% - 2rem)',
    backgroundColor: 'hsl(231, 16%, 92%)',
    boxShadow:
      '0.2em 0.2em calc(0.2em * 2) #A3A7BD, calc(0.2em * -1) calc(0.2em * -1) calc(0.2em * 2) #FFFFFF',
    border: 'none',
    borderRadius: '1rem',
    maxWidth: '475px',
    maxHeight: '650px',
    position: 'relative',
  },
}

export default App
